<template>
  <div class="ao-deal">
    <div class="b">{{ db_1_txt[0] }} {{ balance }} {{ get_c_1 }}</div>
    <el-form ref="form" :model="formBuy" label-width="50px">
      <el-form-item :label="mark == 1 ? db_1_txt[1] : db_1_txt[2]">
        <el-input
          v-model="formBuy.price"
          :disabled="!!priceType"
          :placeholder="pl"
          @focus="focus"
          @blur="blur"
          @input="inputPrice"
        >
          <template slot="append">{{ usdt }}</template>
        </el-input>
      </el-form-item>
      <el-form-item :label="mark == 1 ? db_1_txt[3] : db_1_txt[4]">
        <el-input v-model="formBuy.amount" @input="inputAmount">
          <template slot="append">{{ currency }}</template>
        </el-input>
      </el-form-item>
      <!-- <el-form-item label="密码">
        <el-input v-model="formBuy.password" placeholder="请输入交易密码"></el-input>
      </el-form-item>-->
    </el-form>
    <el-slider v-model="slider" :step="20" show-stops @change="change"></el-slider>
    <div class="c">{{ db_1_txt[5] }} {{ total }} {{ usdt }}</div>
    <el-button :class="btnType" type="primary" @click="event">{{ btnText }}{{ currency }}</el-button>
  </div>
</template>

<script>
export default {
  props: {
    usdt: String,
    currency: String,
    mark: Number,
    btnType: String,
    btnText: String,
    priceType: {
      type: Number,
      default: 0
    },
    price: {
      type: String,
      default: ""
    },
    balance: {
      type: String,
      default: "0.0000"
    },
    setPrice: {
      type: String,
      default: ""
    },
    dealRate: Number
  },
  computed: {
    get_c_1() {
      return this.mark === 1 ? this.usdt : this.currency;
    },
    pl() {
      return this.priceType === 0
        ? ""
        : this.btnType === "buy"
        ? this.db_1_txt[6]
        : this.db_1_txt[7]
    }
  },
  watch: {
    setPrice(a, b) {
      if (a != b && a) {
        this.is_focus = true;
        this.formBuy.price = a;
      }
    },
    price(a, b) {
      if (!b) this.is_focus = false;
      if (!this.is_focus) this.formBuy.price = this.price;
    },
    priceType(a, b) {
      if (!b) this.is_focus = false;
      if (!this.is_focus)
        this.formBuy.price = this.priceType === 0 ? this.price : "";
    }
  },
  data() {
    var that = this;
    return {
      db_1_txt: this.$t('deal.db_1_txt'), // ['可用', '买入价', '卖出价', '买入量', '卖出量', '交易额', '以市场最优价格买', '以市场最优价格卖'],
      is_focus: false,
      slider: 0,
      total: "0.0000",
      formBuy: {
        price: that.price,
        amount: "0",
        password: ""
      }
    };
  },
  methods: {
    inputPrice() {
      this.slider = 0
      var price = this.formBuy.price || 0
      var amount = this.formBuy.amount || 0
      this.total = this.ffff(price * amount, 4);
    },
    inputAmount() {
      this.inputPrice()
    },
    focus() {
      this.is_focus = true;
    },
    blur() {
      if (!this.formBuy.price) {
        this.is_focus = false;
      }
    },
    change() {
      var price = this.formBuy.price
      if (this.mark === 1) {
        var amount = this.slider / 100 * (this.balance / (price - 0 + this.dealRate / 100))
        this.formBuy.amount = this.ffff(amount, 0);
        this.total = this.ffff(this.formBuy.amount * price, 4);
      } else {
        this.formBuy.amount = this.ffff((this.slider / 100) * this.balance, 0);
        this.total = this.ffff(this.formBuy.amount * price, 4);
      }
    },
    ffff(v, l = 8) {
      l = 4
      return parseFloat(v).toFixed(l);
    },
    event() {
      this.formBuy.price_type = this.priceType;
      this.formBuy.mark = this.mark;
      this.formBuy.currency = this.currency;
      this.$emit("event", this.formBuy);
    }
  }
};
</script>

<style lang="scss">
.ao-deal {
  .b {
    height: 48px;
    border-bottom: 1px solid #303b4b;
    line-height: 48px;
    font-size: 14px;
    color: #c7cce6;
  }
  .c {
    color: #c7cce6;
    margin-top: 10px;
    font-size: 16px;
  }
  .el-form-item {
    position: relative;
    line-height: 30px;
    margin-top: 15px;
    margin-bottom: 0;
  }

  .el-form-item__label {
    text-align: left;
    padding: 0;
    font-size: 14px;
    color: #637085;
  }
  .el-input__inner {
    border: 1px solid #2e3753;
    border-radius: 3px;
    height: 38px;
    text-indent: 15px;
    font-size: 16px;
    color: #cdd6e4;
    background-color: #1a243b;
    line-height: 38px;
    padding: 0;
  }
  .el-input.is-disabled .el-input__inner {
    background-color: #1a243b;
    color: #2e3753;
    border: 1px solid #2e3753;
    color: 12px;
  }
  .el-input-group__append {
    color: #637085;
    font-size: 16px;
    border: none;
    padding: 0 0 0 8px;
    background: transparent;
  }
  .el-slider__runway {
    background-color: #2e3244;
  }
  .el-button {
    width: 100%;
    height: 40px;
    border-radius: 3px;
    color: #cdd6e4;
    border: none;
    margin-top: 10px;
    font-size: 16px;
    cursor: pointer;
    &.buy {
      background-color: #12b886;
    }
    &.sell {
      background-color: #cc4951;
    }
  }
}
</style>



